//설정
@mixin flex($x: center, $y: center){
    display: flex;
    justify-content: $x; align-items: $y;
    @content;
}//@mixin flex($x: center, $y: center)

@mixin font($a: inherit, $b: inherit, $c:inherit){
    font-size: $a;
    font-weight: $b;
    color: $c;
    @content;
}//@mixin font($a: inherit, $b: inherit, $c:inherit)

@mixin padding($up: 100px, $LR: 30px, $down: 0px){
    padding: $up $LR $down;
    @content;
}//@mixin padding($up: 100px, $LR: 30px, $down: 0px)

