//초기화
*{
    box-sizing: border-box;
    padding: 0; margin: 0;
    list-style-type: none;
}

html{
    background-color: black;
}

a{
    text-decoration: none;
}

button{
    cursor: pointer;
}


//= = = = = = = = = = = = = = = = = = = = = = = = = 
//작업
div#root{

}

header{
    // outline: 1px solid black;
    position: fixed; top: 0; left: 0; z-index: 99;
    width: 100%;
    padding: 15px;

    @include flex(space-between);
    // background-color: black;
    
    >a.Logo{
        >h1{
            display: inline-block;
            @include font(2rem, 700,white){
                white-space: nowrap;
            };
            transition: .1s;
        }

        &:hover{
            >h1{
                color: red;
            }
        }
    }
    >nav{
        // outline: 1px solid red;
        display: inline-block;
        >a{
            position: relative;
            @include font(inherit, 700,white){
                white-space: nowrap;
            };

            text-align: center;
            margin: 0 10px;

            transition: .2s;

            &:before{
                content: '';
                position: absolute; left: 50%; bottom: -3px;
                display: inline-block;
                background-color: red;
                width: 0%; height: 1px;

                transition: .3s;
            }
            &:hover{
                color: red;
                &:before{
                    left: -5%;
                    width: 110%;
                }
            }
        }

    }//nav
}//header

main{
    height: auto;
    background-color: black;

    >section.popularMovie{
        // outline: 1px solid white;
        width: 100%; height: 50vw;
        max-height: 600px;
        min-height: 450px;
        
        margin-bottom: 5vw;
        >.popularSwiper{
            width: 100%; height: 100%;  
            overflow: hidden;
            
            >.swiper-wrapper{
                width: 100%; height: 100%;
                @include flex(flex-start);
                
                >.swiperLi{ 
                    position: relative; top: 0; left: 0; z-index: 0;
                    cursor: pointer;
                    // border: 1px solid red;
                    width: 100%; height: 100%;
                    flex-shrink: 0;

                    padding: 100px 30px 0;
                    
                    &::before{//검은 배경
                        position: absolute; top: 0; left: 0; z-index: 0;
                        content: '';
                        width: 100%; height: 100%;
                        background-color: rgba(0, 0, 0, 0.6);
                    }//before

                    &::after{//아래 검은 그라데이션 효과
                        position: absolute; bottom: 0; left: 0; z-index: 1;
                        content: '';
                        width: 100%; height: 30%;
                        background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .6), rgba(0, 0, 0, 1));

                    }//after

                    >.content{
                        position: relative; top: 0; left: 0; z-index: 2;
                        >h2.title{
                            @include font(3.5rem, bold, white);

                            word-break: keep-all;
                            word-wrap: break-word;
                        }
                        >p{
                            margin: 30px 0;
                            width: 580px; height: 30%;
                            @include font(1rem, inherit, white);

                            word-break: keep-all;
                            word-wrap: break-word;

                            @media (max-width: 950px){
                                width: 90%;
                            }
                        }
                        >div.btnWrapper{
                            >button{
                                @include font(1rem, bold, white);
                                border-radius: 12px;

                                padding: 7px 20px;
                                margin-right: 10px;
                                transition: .3s;

                                
                                &:nth-of-type(1){
                                    border: 2px solid red; 
                                    background-color: red;
                                    box-shadow: 0 0 7px 8px rgba(255,0,0,.3);
                                    &:hover{
                                        box-shadow: 0 0 7px 15px rgba(255,0,0,.3);
                                    }
                                }
                                &:nth-of-type(2){
                                    border: 2px solid white; 
                                    background-color: transparent;
                                    
                                    &:hover{
                                        background-color: white;
                                        color: red;
                                    }
                                }
                            }//button
                        }
                    }//.content
                }//.swiperLi //li같은 거
            }//.swiper-wrapper //ul같은 거
        }
    }//section.popularMovie

    >section.MovieList{
        >article{
            &:not(:last-child){
                margin-bottom: 0px;
            }
            // outline: 1px solid red;
            padding: 20px 10px;
            height: auto;
            >div.header{
                // outline: 1px solid white;
                @include flex(space-between);
                padding-bottom: 25px;
                >h3{
                    @include font(1.2rem, bold, white);
                }
                
                >button{
                    @include font(.7rem, inherit, white);
                    background-color: transparent;
                    border: 1px solid white; 
                    border-radius: 12px;

                    padding: 3px 10px;
                    margin-right: 10px;
                    transition: .3s;

                    &:hover{
                        background-color: white;
                        color: red;
                    }
                }
            }//div.header

            >.thumList{
                // border: 1px solid white; 
                // height: 52vw;
                >.mySwiper{
                    overflow: hidden;
                    // border: 1px solid white; 
                    height: 100%;
                    cursor: pointer;
                    
                    >.swiper-wrapper{
                        @include flex(flex-start, flex-start);
                        width: 20%; height: 100%;
                        
                        >.SwiperSlide{
                            flex-shrink: 0;
                            width: 100%; height: 100%;
                            >figure{
                                width: 100%; height: 100%;
                                >p{
                                    position: relative; top: 0; left: 0;
                                    // border: 1px solid white;
                                    width: 100%;  height: 80%;
                                    border-radius: 7%;
                                    overflow: hidden;
                                    
                                    >.hoverBg{
                                        position: absolute; top: 0; left: 0;
                                        width: 100%; height: 100%;
                                        background-color: rgba(0, 0, 0, .8);
                                        opacity: 0%;

                                        transition: .3s;
                                        
                                        >.YoutubeShape{//유튜브 모양
                                            position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
                                            display: inline-block;
                                            width: 0%; height: 0%;
                                            border-radius: 10px;
                                            background-color: red;
                                            box-shadow: 0 0 7px 8px rgba(255,0,0,.3);
                                            transition: .3s;
                                            
                                            &::after{//삼각형
                                                box-sizing: content-box;
                                                position: absolute; top: 50%; left: 50%; transform: translate(-25%, -50%);
                                                content: '';
                                                border: 5px solid transparent;
                                                border-left: 10px solid black
                                            }
                                        }//.YoutubeShape
                                    }//.hoverBg

                                    >img{
                                        border: none;
                                        width: 100%; height: 100%;
                                    }

                                }

                                >figcaption{
                                    margin-top: 10px; 
                                    @include font(.8rem, bold, white);
                                }
                            }//figure

                            &:hover{
                                .hoverBg{
                                    opacity: 100%;

                                    .YoutubeShape{//유튜브 모양
                                        width: 25%; height: 10%; 
                                    }
                                }

                                figcaption{
                                    color: red;
                                }
                            }//SwiperSlide:hover

                        }//SwiperSlide // li같은 애
                    }//.swiper-wrapper // ul같은 애
                }//.mySwiper
            }//div.thumList
        }//article
    }//section.MovieList


// = = = = = = = = = = = = = = = = = = = = = = = = = = = = = =


    >.movieNTv{
        @include padding();
        >h2{
            text-align: center;
            @include font(1.2rem, 700,white){
                white-space: nowrap;
            };
        }//h2

        >section.search{
            margin-top: 40px;

            >.searchInputWrapper{
                position: relative; top: 0; left: 0;
                display: inline-block;

                width: 140px;
                margin-right: 15px;

                >input[type='text']{
                    width: 100%;

                    color: gray;
                    background-color: transparent;
                    border: none;
                    border-bottom: 1px solid gray;
                    padding: 5px;

                    transition: .3s;

                    &:focus{
                        outline: none;

                        & ~ .InputfocusLine{
                            width: 100%;
                        }
                    }

                }//input[type='text']

                >.InputfocusLine{
                    position: absolute; bottom: 0; left: 0;

                    display: inline-block;
                    width: 0%; height: 1px;
                    background-color: red;

                    transition: .3s;
                }//.InputfocusLine
            }//.searchInputWrapper

            >button{
                @include font(.8rem, inherit, white);
                border-radius: 9px;

                padding: 2px 12px;
                margin-right: 10px;
                border: 2px solid red;
                background-color: red;
                box-shadow: 0 0 7px 6px rgba(255,0,0,.3);
                
                transition: .3s;
                
                &:hover{
                    box-shadow: 0 0 7px 10px rgba(255,0,0,.3);
                }
            }//button
        }//section.search

        >section.thumList{
            width: 100%;
            margin-top: 20px; 
            >ul{
                // outline: 1px solid white;
                @include flex(flex-start){flex-wrap: wrap; gap: 10px;};
                >li{
                    // outline: 1px solid white;
                    height: 250px;  
                    flex-shrink: 0;
                    cursor: pointer;
                    
                    >figure{
                        width: 100px; height: 200px;
                        >p{
                            position: relative; top: 0; left: 0;
                            // outline: 1px solid white;
                            width: 100%; height: 80%;
                            border-radius: 7%;
                            overflow: hidden;

                            >img{
                                border: none;
                                width: 100%; height: 100%;
                                @include font(.7rem, bold, white);
                            }

                            >.hoverBg{
                                position: absolute; top: 0; left: 0;
                                width: 100%; height: 100%;
                                background-color: rgba(0, 0, 0, .8);
                                opacity: 0%;

                                transition: .3s;
                                
                                >.YoutubeShape{//유튜브 모양
                                    position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%);
                                    display: inline-block;
                                    width: 0%; height: 0%;
                                    border-radius: 5px;
                                    background-color: red;
                                    box-shadow: 0 0 7px 8px rgba(255,0,0,.3);
                                    transition: .3s;
                                    
                                    &::after{//삼각형
                                        box-sizing: content-box;
                                        position: absolute; top: 50%; left: 50%; transform: translate(-25%, -50%);
                                        content: '';
                                        border: 5px solid transparent;
                                        border-left: 10px solid black
                                    }
                                }//.YoutubeShape
                            }//.hoverBg
                        }

                        >figcaption{
                            margin-top: 10px; 
                            @include font(.9rem, bold, white);
                        }
                    }//figure

                    &:hover{
                        .hoverBg{
                            opacity: 100%;

                            .YoutubeShape{//유튜브 모양
                                width: 35%; height: 15%; 
                            }
                        }

                        figcaption{
                            color: red;
                        }
                    }//SwiperSlide:hover
                }//li
            }//ul
        }//section.thumList
    }//.movieNTv

// = = = = = = = = = = = = = = = = = = = = = = = = = = = = = =

    >.Detail{
        position: relative; top: 0; left: 0; z-index: 0;
        outline: 1px solid red;
        
        color: white;
        
        >.banner{
            // outline: 1px solid blue;

            position: absolute; top: 0; left: 0; z-index: 0;
            width: 100%; height: 50vw;
            max-height: 600px; min-height: 450px;
            
            &::before{//검은 배경
                position: absolute; top: 0; left: 0; z-index: 0;
                content: '';
                width: 100%; height: 100%;
                background-color: rgba(0, 0, 0, 0.4);
            }//before

            &::after{//아래 검은 그라데이션 효과
                position: absolute; bottom: 0; left: 0; z-index: 1;
                content: '';
                width: 100%; height: 30%;
                background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .6), rgba(0, 0, 0, 1));

            }//after
        }//banner

        >.content{
            position: relative; top: 0; left: 0; z-index: 1;
            // outline: 1px solid white;
            @include padding(200px, 30px, 50px);

            
            >section.descriptionWrapper{
                // outline: 1px solid white;
                
                width: 100%; height: auto;
                max-height: 500px; 
                @include flex(space-between, flex-start);
                margin-bottom: 200px;
                
                >p.mainPoster{
                    width: 25%; height: 35vw; max-height: 500px; 
                    // outline: 1px solid white;
                    border-radius: 7%;
                    overflow: hidden;
                    >img{
                        width: 100%; height: 100%;
                        object-fit: cover;
                    }
                }//p.mainPoster

                >div.description{
                    padding-top: 10px;

                    width: 70%; height: 100%;
                    // outline: 1px solid white;
                    >h1{
                        @include font(4rem, bold, white);
                        margin-bottom: 32px;
                    }

                    >ul.genres{
                        // outline: 1px solid white;
                        
                        @include flex(flex-start, flex-start){flex-wrap: wrap; row-gap: 5px;};
                        margin-bottom: 32px;

                        >li{
                            background-color: black;
                            border: 1px solid white; border-radius: 20px;
                            padding: 8px 24px;

                            &:not(:last-child){
                                margin-right: 10px;
                            }
                        }
                    }//ul.genres

                    >.overview{
                        margin-bottom: 32px;

                    }//.overview

                    >.castWrapper{
                        // outline: 1px solid white;
                        >h2{
                            // outline: 1px solid white;
                            @include font(1.5rem, bold, white);
                        }

                        >ul.casts{
                            // outline: 1px solid white;
                            @include flex(flex-start, flex-start){flex-wrap: wrap;}
                            >li{
                                width: 110px;
                                // outline: 1px solid white;

                                &:not(:last-child){
                                    margin-right: 15px;
                                }

                                >p{
                                    // outline: 1px solid white;
                                    width: 100%; height: 150px;
                                    >img{width: 100%; height: 100%;}
                                }//p

                                >span.name{
                                    font-size: .7rem;
                                }
                            }//li
                        }//ul.casts
                    }//.castWrapper

                }//div.description
            }//section.descriptionWrapper

            >section.videoContain{
                // outline: 1px solid white;
                @media (min-width: 1400px) {
                    margin: 0 auto;
                    width: 1400px;
                }
                >article.videoWrapper{
                    // outline: 1px solid white;
                    &:not(:last-child){
                        margin-bottom: 50px;
                    }

                    >h2{
                        // outline: 1px solid white;
                        margin-bottom: 32px;
                    }
                    >iframe.video{
                        // outline: 1px solid white;
                        border: none;
                        height: 50vw; max-height: 700px;

                    }//.video
                }//article.videoWrapper
            }//section.videoContain
        }//.content
    }//.Detail
}//main
